@import url('https://fonts.googleapis.com/css?family=Russo+One');
@import url('https://fonts.googleapis.com/css?family=Roboto');

.channel-card {
  width: 95%;
  height: 150px;
  padding: 5px;
  margin: 10px auto;
  box-shadow: rgba(0, 0, 0, 0.156863) 0 3px 10px;
  background-color: white;

  .channel-card--info-wrapper {
    width: 75%;
    display: inline-block;
    padding-left: 10px;

    p {
      margin:0
    }

    .channel-card--followers {
      display: inline-block;
    }

    .channel-card--user-name {
      font: 125% 'Russo One', sans-serif;
      text-decoration: none;
    }

    .channel-card--user-name:link {
      color: black;
    }

    .channel-card--user-name:visited {
      color: black;
    }

    .channel-card--user-name:hover {
      color: rebeccapurple;
      text-decoration: underline;
    }

    .channel-card--status {
      font: italic bold 65% sans-serif;
    }

    .channel-card--streaming {
      font-family: 'Roboto', sans-serif;
      position: absolute;
      top: 50px;
    }
  }
}

.channel-card:hover {
  box-shadow: rgba(102, 51, 153, 0.5) 0 5px 15px;
}

.channel-card__not-found {
  @extend .channel-card;
  background-color: red;
  color: white;
  height: auto;

  .channel-card--message__not-found {
    padding: 0;
    margin: 0;
    text-align: center;
  }
}



